<template>
  <div class="list-table">
  <el-table @selection-change="handleSelectionChange" :data="tableData" style="width: 100%" :class="clazz" :row-class-name="rowClazz">
    <el-table-column v-if="selectable" type="selection" width="55" />
    <el-table-column v-if="showIndex" type="index" label="#" width="50" />
    <template v-for="field in fields">
      <el-table-column :prop="field.VALUE" :label="field.NAME" :class-name="field.clazz" label-class-name="jaswanth" :sortable="isFieldSortable(field)" />
    </template>
  </el-table>
  </div>
</template>

<script>
import SimpleSearch from "@/admin/packages/components/search/SimpleSearch";

// const emit = defineEmits(['close', 'unClose']);
export default {
  name: "ListTable",
  components: {SimpleSearch},
  props: {
    tableData: { type: Array, default: () => [] },
    fields: { type: Array, default: () => [] },
    clazz: { type: String, default: ""},
    rowClazz: { type: Function, default: () => "" },

    selectable: { type: Boolean, default: false },
    showIndex: { type: Boolean, default: false },
    sortable: { type: Boolean, default: false }

  },
  data(){
    return{

    }
  },
  computed: {

  },
  methods: {
    isFieldSortable(field){
      return this.sortable && (field.hasOwnProperty("sort") ? field.sort : true);
    },
    handleSelectionChange(selected){
      if(typeof selected === undefined) return;
      this.$emit("multiselect", selected);
    }
  }
}
</script>

<style lang="css">
.el-table .el-table__header tr th{
  background: #ececec;
  color: #1e1e1e;
}
</style>